// Component for the Flag Toggle
interface FlagToggleSwitchProps {
  isFlagged: boolean;
  onToggle: () => void;
  disabled?: boolean;
}

const FlagToggleSwitch: React.FC<FlagToggleSwitchProps> = ({ isFlagged, onToggle, disabled }) => {
  return (
    <Tooltip title={isFlagged ? "Unflag Item" : "Flag Item"} placement="top">
      {/* Add span to allow Tooltip when Switch is disabled */} 
      <span> 
        <MuiSwitch
          checked={isFlagged}
          onChange={onToggle}
          disabled={disabled}
          size="small"
          onClick={(e) => e.stopPropagation()} // Prevent row click when toggling
        />
      </span>
    </Tooltip>
  );
};

// Component for the Flag Toggle
const FlagToggle = ({ isChecked, onChange }: { isChecked: boolean; onChange: (checked: boolean) => void }) => (
  <div style={{ marginLeft: 'auto', display: 'flex', alignItems: 'center' }}> {/* Push to the right */} 
    <label htmlFor="flagToggle" style={{ marginRight: '8px', fontWeight: 'bold' }}>Show Flagged Only:</label>
    <input 
      type="checkbox" 
      id="flagToggle"
      checked={isChecked}
      onChange={(e) => onChange(e.target.checked)}
      style={{ width: '20px', height: '20px' }} // Basic styling for visibility
    />
  </div>
);

